import React, { Component } from 'react';
import {TweenMax, Power4, TimelineMax} from "gsap";
import Typed from 'typed.js';

export class FirstSection extends Component {
  componentDidMount(){
    let typed = new Typed('#typed', {
      strings: ["a front end developer.", "and eventually a full stack web developer."],
      typeSpeed: 50,
      backSpeed:30,
      startDelay:2000,
    });
    typed.start();
    let intro = document.getElementById('introduction');
    let text = document.getElementById('credit');
    TweenMax.from(text,2,{x:-60});
    TweenMax.from(intro,2,{y:-50,ease:Power4.easeOut});
    TweenMax.to(intro,2,{opacity:1,ease:Power4.easeOut});

  }
  render() {
    return (
      <div className="Section-One">
        <div className='layer'/>
        <span id='introduction'><span id='name'>Hey, I'm Fabrizio.</span><br/> I am currently a student and would<br/> love to become <span id='typed'/> </span>
        <span id='credit' >Photo by Dillan Choiniere on Unsplash</span>
      </div>
    );
  }
}



